@use "styles/stylesFunctions.scss" as f;
@use "styles/sassVariables" as v;

.card {
  border: 0.5px solid #707070;
  padding-top: 49px;
  padding-right: 31px;
  padding-bottom: 35px;
  padding-left: 20px;

  max-width: 700px;
  margin: 0 auto;

  margin-top: 50px;

  transition: v.$transition;

  .card-header {
    display: flex;
    justify-content: space-between;
    align-items: center;

    h3 {
      margin: 0;
      font-size: 30px;
      font-weight: 600;
    }
    span {
      color: v.$primary;
      font-weight: 500;
    }
    p {
      color: red;
      font-size: 25px;
      font-weight: 600;
    }

    .header-wrapper {
      width: 100%;
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      align-items: center;
    }
  }
  .card-content {
    display: flex;
    justify-content: space-between;
    align-items: center;
    span {
      color: v.$primary;
      font-size: 25px;
      font-weight: 400;
    }
    strong {
      font-size: 25px;
      font-weight: 400;
    }

    button {
      display: inline-block;
      padding: 16px 6vw;
      width: fit-content;
      background-color: v.$secondary;
      border: none;
      color: white;
      text-align: center;
      text-decoration: none;
      border-radius: 5px;

      margin-top: 7px;

      transition: v.$transition;

      &:hover {
        background-color: v.$primary;
        border: none;
        color: white;
        transform: scale(0.9);
      }
    }
  }
  &:hover {
    background-color: #70707030;
    // transform: scale(0.9);
  }
}
